import React from 'react';
import TopBarButton from './TopBarButton';
import IconSave from './IconSave';
const TopBarSave = (props) => {
    const { composing, notes, appTheme } = props;
    const save = () => {
        // @ts-ignore
        window.postMessage('save', JSON.stringify({
            notes: notes,
            theme: appTheme
        }));
    };
    return (composing
        ? React.createElement("div", { className: 'c-topbar__control' },
            React.createElement(TopBarButton, { onClick: save, className: 'c-topbar__button--save', icon: React.createElement(IconSave, null) }))
        : null);
};
export default TopBarSave;
